


  .loadingIconInPlayer {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }


@mixin rings($duration, $delay) {
  opacity: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -8px;
  left: -8px;
  right: 0;
  bottom: 0;
  content: '';
  height: 100%;
  width: 100%;
  border: 18px solid white;
  border-radius: 100%;
  animation-name: ripple;
  animation-duration: $duration;
  animation-delay: $delay;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(.65,0,.34,1);
  z-index: -1;
}

.request-loader {
position: relative;
display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
height: 250px;
width: 250px;
border-radius: 100%;
background: transparent;
box-shadow: 0 0 20px 0 rgba(0,0,0,0.25);

span {
  position: relative;
  font-size: 30px;
  top: -7px;
  left: -5px;
  color:white;
}

&::after {
  @include rings(3s, 0s);
}

&::before {
  @include rings(3s, 0.5s);
}
}


@keyframes ripple {
from {
  opacity: 1;
  transform: scale3d(0.75,0.75,1);
}

to {
  opacity: 0;
  transform: scale3d(2.5,2.5,1);
}
}

